import {
  Component,
  OnInit,
  Input,
  Output,
  OnDestroy,
  EventEmitter,
  ViewChild,
  ViewContainerRef
} from '@angular/core';
import {
  SharedModuleEventHandlerService,
  sharedModuleEventHandlerType
} from '@shared-event-handler';
import { SwiperOptions } from 'swiper';
import { LessonPlayerService } from '../../lesson-player.service';
import {
  RevisePlayerResourceInterface,
  RevisePlayerSubResourceInterface,
  SubResourceState
} from 'apps/main/src/app/core/root-store';
import { PlayerFacade } from '../../lesson-player-facade';
import { Subject } from 'rxjs';
import { takeUntil, take } from 'rxjs/operators';

@Component({
  selector: 'app-summary-player',
  templateUrl: './summary-player.component.html',
  styleUrls: ['./summary-player.component.scss']
})
export class SummaryPlayerComponent implements OnInit, OnDestroy {
  @Input() noteArray;
  @Output() continueClicked = new EventEmitter();
  destroy$: Subject<boolean> = new Subject<boolean>();
  dummydata = {
    tqId: 'tq-136801',
    resourceId: 'tq-136758-summary-1',
    resourceType: 'note',
    data: [
      {
        id: 'tq-136760-summary-1',
        data: 'api/revise/summary/tq-136760-summary-1'
      }
    ]
  };
  @Output() summaryCompleted = new EventEmitter();
  @Output() return = new EventEmitter();
  config: SwiperOptions;

  @ViewChild('usefulSwiper', { static: false }) usefulSwiper;
  public data;
  public counter;
  subResource;
  constructor(
    private playerFacade: PlayerFacade,
    private sharedModuleEventHandlerService: SharedModuleEventHandlerService,
    public api: LessonPlayerService
  ) {}

  ngOnInit(): void {
    this.counter = 0;
    this.initSlider();
    if (!this.noteArray) {
      console.log(this.noteArray);
      this.noteArray = this.dummydata;
    }
    this.loadSubResource(this.noteArray, this.counter);
    this.sharedModuleEventHandlerService.on(
      sharedModuleEventHandlerType.CONTINUE_FLOW,
      event => {
        if (this.counter >= this.noteArray.data.length - 1) {
          this.summaryCompleted.emit();
        } else {
          this.nextSummarySlide();
        }
      }
    );
  }

  markCompleted() {
    this.return.emit({});
  }

  nextSummarySlide() {
    this.counter = this.counter + 1;
    this.loadSubResource(this.noteArray, this.counter);
    // this.usefulSwiper.swiper.slideNext();

    // setTimeout(() => {
    //   this.counter = this.counter + 1;
    //   this.data = this.noteArray[this.counter];
    // }, 1000);
  }

  initSlider() {
    this.config = {
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
        type: 'bullets'
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
      },
      on: {
        slideChange: () => {
          console.log(
            'slideChange Event: Active Slide Index = ',
            this.usefulSwiper.swiper.activeIndex
          );
        },
        slideChangeTransitionEnd: () => {
          console.log('slideChange Event');
        }
      },
      spaceBetween: 30
    };
  }

  /*
Sushant Kanaujiya
Load summary data call from api 
*/
  loadSubResource(resource: RevisePlayerResourceInterface, index) {
    this.playerFacade
      .isSubResourcePresent(resource.data[index])
      .pipe(take(2), takeUntil(this.destroy$))
      .subscribe(subResource => {
        if (!subResource) {
          const subResource: RevisePlayerSubResourceInterface = {
            data: resource.data[index],
            resourceType: resource.resourceType,
            subResourceId: resource.data[index].id,
            tqId: resource.tqId
          };
          this.playerFacade.setSubResourceData(subResource);
        } else {
          this.subResource = subResource;
          this.data = subResource.data;
        }
      });
  }

  updateSubResource() {
    let subRes = { ...this.subResource };
    subRes.subResourceState = SubResourceState.COMPLETED;
    this.playerFacade.updateSubResourceState(subRes);
  }

  continueTrigger() {
    this.continueClicked.emit();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
    this.sharedModuleEventHandlerService.off(
      sharedModuleEventHandlerType.CONTINUE_FLOW
    );
  }
}
