import { Injectable } from '@angular/core';
import { LessonPlayerService } from './lesson-player.service';
import { Observable, Subject } from 'rxjs';
import { select, Store } from '@ngrx/store';
import {
  revisePlayerActions,
  RootStoreState,
  revisePlayerSelector,
  TqInterface,
  RevisePlayerResourceInterface,
  browseBookSelectors,
  ResourceInterface,
  RevisePlayerSubResourceInterface,
  myLessonSelectors,
  MyLessonInterface,
  BrowseBookConfigInterface,
  MyLessonConfigInterface
} from '@root-store';

@Injectable()
export class PlayerFacade {

  constructor(
    private lessonPlayerService: LessonPlayerService,
    private store: Store<RootStoreState.State>
  ) {}

  onInit(){
    this.lessonPlayerService.onInit();
  }

  getGuidanceFlag() {
    return this.lessonPlayerService.getGuidanceFlag();
  }

  setGuidanceFlag(flag) {
    this.lessonPlayerService.setGuidanceFlag(flag);
  }

  getTqsData(): Observable<TqInterface[]> {
    return this.store.pipe(select(revisePlayerSelector.selectAllTqs));
  }

  setSubResourceData(resource: RevisePlayerSubResourceInterface) {
    this.store.dispatch(new revisePlayerActions.GetSubResource(resource));
  }

  updateTqState(tq: TqInterface) {
    this.store.dispatch(
      new revisePlayerActions.UpdateTqState({
        id: tq.tqId,
        changes: tq
      })
    );
  }

  updateResourceState(resource: RevisePlayerResourceInterface) {
    this.store.dispatch(
      new revisePlayerActions.UpdateResourceState({
        id: resource.resourceId,
        changes: resource
      })
    );
  }

  updateSubResourceState(subResource: RevisePlayerSubResourceInterface) {
    this.store.dispatch(
      new revisePlayerActions.UpdateSubResourceState({
        id: subResource.subResourceId,
        changes: subResource
      })
    );
  }

  getResourceList(tqId): Observable<RevisePlayerResourceInterface[]> {
    return this.store.pipe(
      select(revisePlayerSelector.getResourceByTq(), {
        tqId: tqId
      })
    );
  }

  isSubResourcePresent(
    subResource
  ): Observable<RevisePlayerSubResourceInterface> {
    return this.store.pipe(
      select(revisePlayerSelector.isSubResourcePresent(), {
        subResource: subResource
      })
    );
  }

  getSelectedResourceFromBrowseBook(): Observable<ResourceInterface> {
    return this.store.pipe(select(browseBookSelectors.getCurrentResource));
  }

  getSelectedLessonFromMyLesson(): Observable<MyLessonInterface> {
    return this.store.pipe(select(myLessonSelectors.getCurrentLesson));
  }

  getLessonState(){
    return this.lessonPlayerService.geLessonState();
  }

  setLessonState(state){
    this.lessonPlayerService.setLessonState(state);
  }

  getOverViewState(){
    return this.lessonPlayerService.getOverViewState();
  }

  setOverViewState(state: number){
    this.lessonPlayerService.setOverViewState(state);
  }

  getTimerState(){
    return this.lessonPlayerService.getTimerState();
  }

  setTimerState(state: number){
    this.lessonPlayerService.setTimerState(state);
  }

  startLessonTimer(){
    return this.lessonPlayerService.startLessonTimer();
  }

  endLessonTimer(){
    return this.lessonPlayerService.endLessonTimer();
  }

  totalTimeTaken(){
    return this.lessonPlayerService.totalTimeTaken();
  }

  getTotalResourceTypeLength(resourceTypes: Array<string>){
    return this.store.pipe(select(revisePlayerSelector.getTotalResourceTypeLength(), {
      resourceType: resourceTypes
    }));
  }

  setTarget(target){
    this.lessonPlayerService.setTarget(target);
  }

  getTarget(){
    return this.lessonPlayerService.getTarget();
  }

  updateTargetType(targetType: string){
    this.lessonPlayerService.updateTargetType(targetType);
  }

  getBrowseBookConfigStateByLessonType(lessonType: string): Observable<BrowseBookConfigInterface>{
    return this.store.pipe(select(browseBookSelectors.getBrowseBookConfigByType(), {
      lessonType: lessonType
    }))
  }

  getMyLessonConfigStateByLessonType(lessonType: string): Observable<MyLessonConfigInterface>{
    return this.store.pipe(select(myLessonSelectors.getMyLessonConfigByType(), {
      lessonType: lessonType
    }))
  }

  getLessonPlayerConfigData(){
    return this.lessonPlayerService.getLessonPlayerConfigData();
  }

  setLessonPlayerConfigData(config){
    this.lessonPlayerService.setLessonPlayerConfigData(config);
  }

  getLessonData(){
    return this.lessonPlayerService.getLessonData();
  }

  setLessonData(lesson){
    this.lessonPlayerService.setLessonData(lesson);
  }

  getTqsByLessonType(lessonType: string){
    return this.lessonPlayerService.getTqsByLessonType(lessonType);
  }

  getSubResourceByKeyValue(key: string, value: string){
    return this.store.pipe(select(revisePlayerSelector.getSubResourceByKeyValue(), {
      key: key, value: value
    }))
  }

  addSubResource(subResource: RevisePlayerSubResourceInterface []){
    this.store.dispatch(new revisePlayerActions.GetSubResourceSuccess(subResource));
  }

  getcurrentTqSelected(){
    return this.lessonPlayerService.getCurrentTqSelected()
  }

  setCurrentTqSelected(tq){
    this.lessonPlayerService.setCurrentTqSelected(tq);
  }

  resetState(){
    this.store.dispatch(new revisePlayerActions.ResetState());
  }

  onDestroy(){
    this.lessonPlayerService.onDestroy();
  }

}
