import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { ButtonState } from '../../models/practice.model';
import { ConstantPool } from '@angular/compiler';
import { PractiseQuizControlsComponent } from '../practise-quiz-controls/practise-quiz-controls.component';
import { PractiseQuestionComponent } from '../practise-question/practise-question.component';
import { QuizControlState } from '../../models/practise.enum';


@Component({
  selector: 'afs-practise-quiz',
  templateUrl: './practise-quiz.component.html',
  styleUrls: ['./practise-quiz.component.scss']
})
export class PractiseQuizComponent implements OnInit {

  @Input() quizData: any = [];
  @Output() quizCompleted = new EventEmitter();

  currQuestion: any;
  questionCounter = 0;
  currQuesAttempts = 0;

  validationState;
  quizcontrolState;

  quizControlStateMap = new Map<string, Array<ButtonState>>();
  selectedQuestionState;

  @ViewChild(PractiseQuizControlsComponent) quizControlInstance;
  @ViewChild(PractiseQuestionComponent) questionComponent: PractiseQuestionComponent;

  constructor() { }

  ngOnInit(): void {

    this.currQuestion = this.quizData[this.questionCounter];

    this.createQuizControlStateMap();

    this.quizcontrolState = this.quizControlStateMap.get(QuizControlState.onQLoad);
  }

  retryHandler() {
    this.questionComponent.validationState = '';
    this.quizcontrolState = this.quizControlStateMap.get(QuizControlState.onQLoad);

  }
  nextQuestion() {
    this.questionCounter++;
    if (this.questionCounter < this.quizData.length) {
      console.log('Answer Submitted');
      this.currQuestion = { ...this.quizData[this.questionCounter] };
      this.quizcontrolState = [...this.quizControlStateMap.get(QuizControlState.onQLoad)];
      this.questionComponent.validationState = '';
    } else {
      // this.questionCounter = 0;
      this.quizCompleted.emit('');
    }
  }

  skipQuestion() {
    this.quizData.splice(this.questionCounter, 1);
    this.quizData.push(this.currQuestion);

    this.currQuestion = { ...this.quizData[this.questionCounter] };
  }


  onQuestionAttempt(selected: string) {
    this.selectedQuestionState = selected;

    this.quizcontrolState = [...this.quizControlStateMap.get(QuizControlState.onAttempt)];
    console.log('onAttempt Quiz control state loaded');
  }

  validateQuestion() {
    // Api or store call to check Validity
    this.currQuestion.attemptCount++;
    if (this.currQuestion.attemptCount > 1) {
      this.quizcontrolState = [...this.quizControlStateMap.get(QuizControlState.onFinal)];
      console.log('fb_final Quiz control state loaded');
      this.questionComponent.validationState = this.selectedQuestionState === 'correct' ?
        'Your Answer is correct' : 'Your Answer is not correct';
    } else if (this.selectedQuestionState === 'correct') {
      this.questionComponent.validationState = 'Your Answer is correct';
      this.quizcontrolState = [...this.quizControlStateMap.get(QuizControlState.onFinal)];
    } else {
      this.questionComponent.validationState = 'Your Answer is not correct';
      this.quizcontrolState = [...this.quizControlStateMap.get(QuizControlState.onQRetry)];
    }
  }

  private createQuizControlStateMap() {
    this.quizControlStateMap.set(QuizControlState.onQLoad, [
      {
        name: 'Check Answer',
        disabled: true,
        handler: () => { },
        styles: ''
      },
      {
        name: 'I’ll attempt later',
        disabled: false,
        handler: this.skipQuestion.bind(this),
        styles: ''
      },
    ]);
    this.quizControlStateMap.set(QuizControlState.onAttempt, [
      {
        name: 'Check Answer',
        disabled: false,
        handler: this.validateQuestion.bind(this),
        styles: ''
      },
      {
        name: 'I’ll attempt later',
        disabled: false,
        handler: this.skipQuestion.bind(this),
        styles: ''
      },
    ]);
    this.quizControlStateMap.set(QuizControlState.onQRetry, [
      {
        name: 'Retry',
        disabled: false,
        handler: this.retryHandler.bind(this),
        styles: ''
      },
      {
        name: `I don't know`,
        disabled: false,
        handler: () => { },
        styles: ''
      },
    ]);
    this.quizControlStateMap.set(QuizControlState.onFinal, [
      {
        name: 'Continue',
        disabled: false,
        handler: this.nextQuestion.bind(this),
        styles: ''
      },
      {
        name: 'Try Similar',
        disabled: false,
        handler: this.nextQuestion.bind(this),
        styles: ''
      },
    ]);
  }


}
