import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PlayerComponent } from './container/player/player.component';
import { SummaryPlayerComponent } from './components/summary-player/summary-player.component';

const routes: Routes = [
  {
    path: '',
    component: PlayerComponent
  },
  {
    path: 'player',
    component: PlayerComponent
  },

  {
    path: 'summary',
    component: SummaryPlayerComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LessonPlayerRoutingModule {}
