import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  Input,
  OnDestroy
} from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import {
  SharedModuleEventHandlerService,
  sharedModuleEventHandlerType
} from '@shared-event-handler';
import { LessonPlayerService } from '../../lesson-player.service';
import {
  RevisePlayerResourceInterface,
  RevisePlayerSubResourceInterface,
  TqState,
  SubResourceState,
  RevisePLayerResourceType
} from '@root-store';
import { PlayerFacade } from '../../lesson-player-facade';
import { take, takeUntil } from 'rxjs/operators';
import * as _ from 'lodash';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
@Component({
  selector: 'app-video-player',
  templateUrl: './video-player.component.html',
  styleUrls: ['./video-player.component.scss']
})
export class VideoPlayerComponent implements OnInit, OnDestroy {
  public destroy$: Subject<boolean> = new Subject<boolean>();
  public play: Subject<void> = new Subject<void>();
  public pause: Subject<void> = new Subject<void>();
  public setTime: Subject<any> = new Subject<any>();
  public updateCue: Subject<any> = new Subject<any>();
  public resetPlayer: Subject<any> = new Subject<any>();
  public showCorrectAns: Subject<any> = new Subject<any>();
  public resetQuestion: Subject<any> = new Subject<any>();
  public removeDisable: Subject<any> = new Subject<any>();
  notePath;
  currCueAction;
  subResource: RevisePlayerSubResourceInterface;
  isText: any = false;
  currVideoPos;
  isNote = false;
  createNotes = false;
  openNoteConfirmation = false;
  hideNodeConfirmation: any;
  videoPlayerCopy: any;
  noteText: any;
  overviewObj: any;
  showSavedNotes = false;
  stuNoteId = '';
  noteData;
  lastShown: any;
  destroyIsPresent: Subscription;
  @Input() videoPlayerDataPath;
  videoPlayerdata: any;
  @Output() videoCompleted: EventEmitter<void> = new EventEmitter<void>();
  @Output() videoSkip = new EventEmitter();
  constructor(
    private playerFacade: PlayerFacade,
    private sharedModuleEventHandlerService: SharedModuleEventHandlerService,
    public api: LessonPlayerService
  ) {}

  ngOnInit(): void {
    this.hideNodeConfirmation = this.playerFacade.getGuidanceFlag();
    this.loadSubResource(this.videoPlayerDataPath, 0);
    this.playerFacade
      .getTqsData()
      .pipe(take(1))
      .subscribe(value => {
        this.overviewObj = value;
      });
    this.sharedModuleEventHandlerService.on(
      sharedModuleEventHandlerType.CONTINUE_FLOW,
      event => {
        this.videoSkip.emit();
      }
    );
  }

  complete() {
    if (this.subResource.subResourceState !== 3) {
      this.playerFacade.updateTargetType(RevisePLayerResourceType.VIDEO);
    }
    this.updateSubResource();
    this.videoCompleted.next();
  }
  onVideoLoaded() {
    this.play.next();
  }

  cuePointTriggerControler(e) {
      this.currCueAction = e;
      const data = {
        resourceId: e.action.params.subContentId
      };
      if (e.action.library === 'note') {
        this.loadNote();
      } else if (e.action.library === 'TEXT') {
        this.loadText(e);
      }
  }

  loadText(e) {
    if (e.action.pause) {
      this.pause.next();
    }
    this.isText = true;
  }

  onVideoTimeChange(time) {
    this.currVideoPos = time;
  }

  setId() {
    this.stuNoteId = `${this.subResource.tqId}_stunote_${new Date().getTime()}`;
  }

  addStudentNote(noteText) {
    const data = {
      title: this.playerFacade.getcurrentTqSelected().label,
      assets: [
        {
          type: 'text',
          value: noteText
        }
      ]
    };
    const subResource: RevisePlayerSubResourceInterface = {
      tqId: this.subResource.tqId,
      resourceType: 'stunote',
      subResourceId: this.stuNoteId,
      data: data
    };
    this.playerFacade.addSubResource([subResource]);
  }

  addNote(noteText) {
    const data = {
      subResourceId: this.stuNoteId,
      action: {
        library: 'TEXT',
        params: {
          text: noteText,
          subContentId: 1
        },
        x: 0,
        y: 0,
        duration: {
          from: this.currVideoPos,
          to: this.currVideoPos + 5
        },
        pause: true,
        label: '',
        width: 50,
        height: 50,
        displayType: 'button',
        visuals: {
          backgroundColor: 'rgb(255, 255, 255)',
          boxShadow: true
        },
        status: true,
        goto: {
          url: {
            protocol: 'http://'
          },
          visualize: false,
          type: ''
        },
        buttonOnMobile: true
      }
    };
    this.updateSubResourceData(data);
    this.cuePointTriggerControler(data);
    this.currVideoPos = this.currVideoPos+1;
    this.setTime.next(this.currVideoPos);
    this.lastShown = data.subResourceId
  }

  updateSubResourceData(addNote) {
    const subRes = _.cloneDeep(this.subResource);
    subRes.data.interactiveVideo.assets.interactions.push(addNote);
    this.updateCue.next(subRes.data.interactiveVideo.assets.interactions);
    this.subResource.data.interactiveVideo.assets.interactions.push(addNote);
    this.playerFacade.updateSubResourceState(subRes);
    
  }

  loadNote() {
    this.notePath = {
      label: 'Note test__',
      resourceId: 216,
      closeBtn: true,
      data: JSON.stringify([
        {
          type: 'text',
          data:
            '<p>Note test description</p><p><br></p><ol><li>Test 1</li><li>Test 2</li></ol>',
          posX: 0,
          posY: 0,
          bg: '',
          style: {}
        }
      ])
    };
    this.isNote = true;
  }

  getStyle() {
    const style = {
      position: 'absolute',
      overflow: 'auto',
      height: this.currCueAction.action.height + '%',
      width: this.currCueAction.action.width + '%',
      top: this.currCueAction.action.y + '%',
      left: this.currCueAction.action.x + '%'
    };
    return style;
  }

  onEndCuePointTrigger() {
    this.isNote = false;
    this.isText = false;
  }

  getPlayerData() {
    this.api
      .getJsonData(this.videoPlayerDataPath)
      .pipe(take(1))
      .subscribe(res => {
        this.videoPlayerdata = res;
      });
  }

  loadSubResource(resource: RevisePlayerResourceInterface, index) {
    this.destroyIsPresent = this.playerFacade
      .isSubResourcePresent(resource.data[index])
      .pipe(takeUntil(this.destroy$))
      .subscribe(subResource => {
        if (!subResource) {
          const subResource: RevisePlayerSubResourceInterface = {
            data: resource.data[index],
            resourceType: resource.resourceType,
            subResourceId: resource.data[index].id,
            tqId: resource.tqId
          };
          this.playerFacade.setSubResourceData(subResource);
        } else {
          this.subResource = JSON.parse(JSON.stringify(subResource));
          if(this.isText){
            this.updateCueText(this.subResource)
          }
          this.updateCue.next(this.subResource.data.interactiveVideo.assets.interactions);
          this.videoPlayerdata = JSON.parse(JSON.stringify(subResource.data));
        }
      });
  }

  updateCueText(obj) {
    obj.data.interactiveVideo.assets.interactions.forEach(element => {
      if(element.subResourceId===this.currCueAction.subResourceId) {
        this.currCueAction = element
      }
    });
  }

  updateSubResource() {
    let subRes = { ...this.subResource };
    subRes.subResourceState = SubResourceState.COMPLETED;
    this.playerFacade.updateSubResourceState(subRes);
  }
  createNotesTrigger() {
    this.pause.next();
    this.createNotes = true;
    this.noteData = {};
  }

  continueClickEvent() {
    this.isText = false;
    this.play.next();
  }

  saveNoteData(saveData) {
    this.setId();
    this.addNote(saveData);
    this.addStudentNote(saveData);
    this.openNoteConfirmation = true;
    this.showSavedNotes = true;
  }

  setGuidanceModalFlag(flag) {
    this.playerFacade.setGuidanceFlag(flag);
    this.hideNodeConfirmation = this.playerFacade.getGuidanceFlag();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
    this.destroyIsPresent.unsubscribe();
    this.sharedModuleEventHandlerService.off(
      sharedModuleEventHandlerType.CONTINUE_FLOW
    );
  }
}
