import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  Input,
  OnDestroy
} from '@angular/core';
import {
  SharedModuleEventHandlerService,
  sharedModuleEventHandlerType
} from '@shared-event-handler';
import {
  RevisePlayerResourceInterface,
  RevisePlayerSubResourceInterface,
  SubResourceState,
  RevisePLayerResourceType
} from '@root-store';
import { PlayerFacade } from "../../lesson-player-facade";
import { take, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-quiz-player',
  templateUrl: './quiz-player.component.html',
  styleUrls: ['./quiz-player.component.scss']
})
export class QuizPlayerComponent implements OnInit, OnDestroy {
  ngOnDestroy(): void {
    this.sharedModuleEventHandlerService.off(
      sharedModuleEventHandlerType.CONTINUE_FLOW
    );
  }
  @Input() quizData;
  @Output() quizCompleted = new EventEmitter();
  @Output() questionAttempted = new EventEmitter();
  @Output() questionLoaded = new EventEmitter();
  @Output() questionCompleted = new EventEmitter();
  public destroy$: Subject<boolean> = new Subject<boolean>();
  public data: any;
  public counter: any;
  subResource: any;
  constructor(
    private playerFacade: PlayerFacade,
    private sharedModuleEventHandlerService: SharedModuleEventHandlerService
  ) {}

  ngOnInit(): void {
    this.counter = 0;
    this.data = this.quizData.data[this.counter];
    this.loadSubResource(this.quizData, this.counter);
    this.sharedModuleEventHandlerService.on(
      sharedModuleEventHandlerType.CONTINUE_FLOW,
      event => {
        console.log(this.quizData);
        console.log(this.counter);
        if (this.quizData && this.counter >= this.quizData.data.length - 1) {
          this.quizCompleted.emit();
        } else {
          this.nextQuestion();
        }
      }
    );
  }

  nextQuestion() {
    this.questionLoaded.emit();
    this.counter = this.counter + 1;
    this.data = this.quizData.data[this.counter];
    this.loadSubResource(this.quizData, this.counter);
  }

  complete() {
    if(this.subResource && this.subResource.subResourceState!==3){
      this.playerFacade.updateTargetType(RevisePLayerResourceType.QUIZ);
    }
    this.updateSubResource();
    this.questionCompleted.emit(this.counter);
  }

  visited() {
    this.questionAttempted.emit();
  }
  loadSubResource(resource: RevisePlayerResourceInterface, index) {
    this.playerFacade
      .isSubResourcePresent(resource.data[index])
      .pipe(take(2), takeUntil(this.destroy$))
      .subscribe(subResource => {
        if (!subResource) {
          let subResource: RevisePlayerSubResourceInterface = {
            data: resource.data[index],
            resourceType: resource.resourceType,
            subResourceId: resource.data[index].id,
            tqId: resource.tqId
          };
          this.playerFacade.setSubResourceData(subResource);
        } else {
          console.log(subResource);
          this.subResource = subResource;
        }
      });
  }

  updateSubResource() {
    let subRes = { ...this.subResource };
    subRes.subResourceState = SubResourceState.COMPLETED;
    this.playerFacade.updateSubResourceState(subRes);
  }
}
