import { FormsModule } from '@angular/forms';
import { ReviseNotesComponent } from './revise-notes.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModuleEventHandlerModule } from '@shared-event-handler';
import { QuillModule } from 'ngx-quill';



@NgModule({
  declarations: [ReviseNotesComponent],
  imports: [
    CommonModule,
    FormsModule,
    SharedModuleEventHandlerModule,
    QuillModule.forRoot({
      modules: {
        syntax: true,
        formula: true, // Include formula module
        //toolbar: [['formula', 'link']] // Include button in toolbar
        toolbar: [
          ["bold", "italic", "underline", "strike"], // toggled buttons
          ["blockquote", "code-block"],

          [{ header: 1 }, { header: 2 }], // custom button values
          [{ list: "ordered" }, { list: "bullet" }],
          [{ script: "sub" }, { script: "super" }], // superscript/subscript
          [{ indent: "-1" }, { indent: "+1" }], // outdent/indent
          [{ direction: "rtl" }], // text direction

          [{ size: ["small", false, "large", "huge"] }], // custom dropdown
          [{ header: [1, 2, 3, 4, 5, 6, false] }],

          [{ color: [] }, { background: [] }], // dropdown with defaults from theme
          [{ font: [] }],
          [{ align: [] }],

          ["clean"], // remove formatting button

          ["link", "image", "video"], // link and image, video
          ["formula", "link"],
        ],
      },
    })
  ],
  exports: [
    ReviseNotesComponent
  ]
})

export class ReviseNotesModule {
  static default = ReviseNotesComponent;
}
