import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  ViewContainerRef,
  ViewChild,
  OnDestroy,
  ComponentFactory
} from '@angular/core';
import { ComponentListService } from '@component-list';
import { Subject, combineLatest } from 'rxjs';
import { takeUntil, map, take } from 'rxjs/operators';
import {
  SharedModuleEventHandlerService,
  sharedModuleEventHandlerType
} from '@shared-event-handler';
import { PlayerFacade } from './../../lesson-player-facade';
import { messages, messagesWithParams } from './../../message-dictionary';
import { RevisePLayerResourceType } from '@root-store';

@Component({
  selector: 'app-lesson-intro',
  templateUrl: './lesson-intro.component.html',
  styleUrls: ['./lesson-intro.component.scss']
})
export class LessonIntroComponent implements OnInit, OnDestroy {
  public headerData: any;
  destroy$: Subject<boolean> = new Subject<boolean>();
  lessonPLayerData = [];
  @ViewChild('componentRef', { read: ViewContainerRef })
  componentRef: ViewContainerRef;
  @Output() navigation: EventEmitter<string> = new EventEmitter<string>();
  openingAndClosingData;

  constructor(
    private componentListService: ComponentListService,
    private sharedModuleEventHandlerService: SharedModuleEventHandlerService,
    private playerFacade: PlayerFacade
  ) {
    this.sharedModuleEventHandlerService.on(
      sharedModuleEventHandlerType.CONTROL_NAVIGATION,
      data => {
        this.navigation.emit(data);
      }
    );

    this.sharedModuleEventHandlerService.on(
      sharedModuleEventHandlerType.LESSON_OVERVIEW_TOGGLE,
      (data: boolean) => {
        this.sharedModuleEventHandlerService.broadcast(
          sharedModuleEventHandlerType.LESSON_OBJECTIVE_TOGGLE,
          data
        );
      }
    );
  }

  ngOnInit(): void {
    this.headerData = {
      text: 'Habitats and Adaptations',
      type: 'home',
      classes: 'small',
      link: '/browseBook/main'
    };
    this.setReviseTargets().then(() => {
      this.prepareOpeningAndClosingData();
      this.loadDynamicComponents();
    });
  }

  setReviseTargets() {
    return new Promise(resolve => {
      this.playerFacade
        .getTotalResourceTypeLength([RevisePLayerResourceType.VIDEO, RevisePLayerResourceType.QUIZ])
        .pipe(take(2), takeUntil(this.destroy$))
        .subscribe(targerts => {
          if (targerts.length > 0) {
            this.playerFacade.setTarget(targerts);
            resolve();
          }
        });
    });
  }

  prepareOpeningAndClosingData() {
    const openingAndClosingData = [];
    const lessonData = this.playerFacade.getLessonData();
    const config = this.playerFacade.getLessonPlayerConfigData();
    const dynamicComponents: Array<string> = config.dynamicComponents;

    if (dynamicComponents.includes('lesson-objectives')) {
      const targetMessages = [];
      const goalState = [];
      const targets = this.playerFacade.getTarget();

      targets.map(target => {
        targetMessages.push({
          message: messages[target.type],
          state: target.state
        });
        goalState.push(target.state);
      });

      const goalCompleted = goalState.reduce((a, b) => a * b);
      const goalMessage = {
        state: goalCompleted,
        message: `Collect ${lessonData.totalGoals} bonus star hidden in the questions`
      };

      const timerState = this.playerFacade.getTimerState();
      const timeTaken = `${this.playerFacade.totalTimeTaken().minutes}.${(
        '0' + this.playerFacade.totalTimeTaken().seconds
      ).slice(-2)}`;
      const timeFormat =
        timerState === 2
          ? lessonData.duration
          : `${timeTaken}/${lessonData.duration}`;
      const timerMessage = {
        state: timerState,
        message: messagesWithParams(`timer_${timerState}`, timeFormat)
      };

      openingAndClosingData.push({
        type: 'lesson-objectives',
        data: {
          title: messages[`overview_${this.playerFacade.getOverViewState()}`],
          data: [
            {
              type: 'time',
              data: timerMessage
            },
            {
              type: 'target',
              data: targetMessages
            },
            {
              type: 'goals',
              data: goalMessage
            }
          ]
        }
      });
    }

    if (dynamicComponents.includes('lesson-overview')) {
      openingAndClosingData.push({
        type: 'lesson-overview',
        data: []
      });
    }

    if (dynamicComponents.includes('lesson-control')) {
      openingAndClosingData.push({
        type: 'lesson-control',
        data: config.controls[this.playerFacade.getLessonState()]
      });
    }
    this.openingAndClosingData = openingAndClosingData;
    console.log('openingAndClosingData', openingAndClosingData);
  }

  loadDynamicComponents() {
    let dynamicComponentObservable = [];
    for (let i = 0; i < this.openingAndClosingData.length; i++) {
      dynamicComponentObservable.push(
        this.componentListService.getComponent(
          this.openingAndClosingData[i].type
        )
      );
    }

    combineLatest(dynamicComponentObservable)
      .pipe(
        map(dynamicComponentSubscriber => dynamicComponentSubscriber),
        takeUntil(this.destroy$)
      )
      .subscribe(componentFactoryList => {
        componentFactoryList.map(
          (componentFactory: ComponentFactory<Component>, i) => {
            const componentRef = this.componentRef.createComponent(
              componentFactory
            );
            const instance = componentRef.instance as any;
            instance.data = this.openingAndClosingData[i].data;
          }
        );
      });
  }

  ngOnDestroy() {
    this.sharedModuleEventHandlerService.off(
      sharedModuleEventHandlerType.CONTROL_NAVIGATION
    );
    this.sharedModuleEventHandlerService.off(
      sharedModuleEventHandlerType.LESSON_OBJECTIVE_TOGGLE
    );
    this.sharedModuleEventHandlerService.off(
      sharedModuleEventHandlerType.LESSON_OVERVIEW_TOGGLE
    );
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}
