import { Component, OnInit, ChangeDetectionStrategy, Input, Output, EventEmitter } from "@angular/core";
import { sharedModuleEventHandlerType, SharedModuleEventHandlerService } from '@shared-event-handler';

@Component({
  selector: "tce-web-note-accordion",
  templateUrl: "./note-accordion.component.html",
  styleUrls: ["./note-accordion.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class NoteAccordionComponent implements OnInit {
  @Input() public data: any;
  @Output() public editEventTrigger = new EventEmitter();
  @Output() public selectedNote = new EventEmitter();
  constructor(private sharedModuleEventHandlerService: SharedModuleEventHandlerService) { }

  public ngOnInit(): void {
  }

  public editEvent(obj) {
    this.editEventTrigger.emit(obj);
  }

  public editResource(obj) {
    this.selectedNote.emit(obj);
  }

}


