import { Component, OnInit, Input, Output, EventEmitter, HostListener } from '@angular/core';

@Component({
  selector: 'app-overview-menu',
  templateUrl: './overview-menu.component.html',
  styleUrls: ['./overview-menu.component.scss']
})
export class OverviewMenuComponent implements OnInit {
  @Input() menuData;
  @Input() selectemenuData;
  @Input() noteLength;
  @Input() selectedResourceObj;
  @Input() currentIndex;
  @Output() selectedTq = new EventEmitter();
  @Output() toggleSummary = new EventEmitter();
  public openOverviewMenu = true;
  public showBtn = true;
  public selectedItem: any;
  public tqIndex = 0;
  menuCopy: any = [];
  @Output() toggleQuit = new EventEmitter();
  @Output() tqExpanded = new EventEmitter();
  @Output() noteTrigger = new EventEmitter();
  // public openOverviewMenu = true;

  constructor() { }

  ngOnInit(): void {
    this.updateMenuItems()
    this.tqIndex = this.currentIndex;
    this.menuCopy[this.tqIndex].subMenuOpen = true;
  }

  menuSelect(selectedTq, selectedResource) {
    this.selectedTq.emit({
      "selectedTq": selectedTq,
      "selectedResource": selectedResource
    });
  }


  public updateMenuItems() {
    this.menuCopy = [];
    for(let i=0;i<this.menuData.length;i++){
      this.menuCopy.push({subMenuOpen : false});
    }
  }

  public setStatusView(obj,i) {
      if ( obj.tqState === 0) {
        return "item locked"
      } 
      else if (obj.tqState > 1) {
          return (!this.menuCopy[i].subMenuOpen)?"item completed":"item completed active"
      } else {
          return (!this.menuCopy[i].subMenuOpen)?"item inprogress":"item inprogress active"
      }
  }

  public toggleSubMenu(obj, index ) {   
    if (this.tqIndex === index) {
      this.tqIndex = this.currentIndex;

    } else {
      this.updateMenuItems();
      this.tqIndex = index;
      this.menuCopy[this.tqIndex].subMenuOpen = true;
      this.tqExpanded.emit({index});
    }
  }

  public quitOverview() {
    this.toggleQuit.next();
  }

  public noteClick() {
    this.noteTrigger.emit();
  }

  isDisabled(value) {
    if (value > 0) {
      return false;
    }
    else {
      return true;
    }
  }
  /**
     * @param targetElement
     * Function to close the Overview Menu when click outside the box
     * Utkarsh
     */

  @HostListener('document:click', ['$event.target'])
  public onClick(targetElement) {
    console.log("click outside");

    const targetInnerElement: HTMLElement = document.getElementById('modal-inner');
    const clickedInside = targetElement.contains(targetInnerElement);
    if (clickedInside) {
      console.log("click inside");
      this.quitOverview();
    }
  }
}
