import {
  Injectable,
  Inject,
  ViewContainerRef,
  ComponentFactoryResolver,
  ModuleWithComponentFactories,
  Injector,
  Compiler,
  NgModuleRef,
  ComponentFactory,
  Component
} from '@angular/core';
import { COMPONENTS } from './component-list.token';
import { ComponentInterface } from './component-list.interface';
import { throwError, Observable } from 'rxjs';

@Injectable()
export class ComponentListService {
  constructor(
    @Inject(COMPONENTS) private componentList: ComponentInterface[],
    private readonly componentFactoryResolver: ComponentFactoryResolver,
    private compiler: Compiler,
    private injector: Injector
  ) {}

  getComponent(componentId: string): Observable<ComponentFactory<Component>> {
    return new Observable(observe => {
      const selectedComponent = this.componentList.filter(
        component => component.componentId === componentId
      );
      if (selectedComponent.length > 0) {
        this.getComponentModule(selectedComponent[0]).then(
          ComponentFactory => observe.next(ComponentFactory),
          error => {
            throw throwError(error);
          }
        );
      } else {
        throw throwError(`Component name: ${componentId} not found!`);
      }
    });
  }

  async getComponentModule(selectedComponent: ComponentInterface) {
    const module = await selectedComponent.loadChildren().then(mod => mod);
    const component = module.default;
    const moduleFactory: ModuleWithComponentFactories<any> = this.compiler.compileModuleAndAllComponentsSync(
      module
    );
    const moduleFactoryRef: NgModuleRef<any> = moduleFactory.ngModuleFactory.create(
      this.injector
    );
    const componentFactory = await moduleFactoryRef.componentFactoryResolver.resolveComponentFactory(
      component
    );
    return componentFactory;
  }

  appendComponentAndGetInstance(
    componentFactory: ComponentFactory<Component>,
    componentRef: ViewContainerRef
  ) {
    const component: any = componentRef.createComponent(componentFactory);
    return component;
  }
}
