import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnDestroy
} from '@angular/core';
import { PlayerFacade } from '../../lesson-player-facade';
import { Subject, Observable, BehaviorSubject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import {
  SharedModuleEventHandlerService,
  sharedModuleEventHandlerType
} from '@shared-event-handler';
import { LessonPlayerService } from '../../lesson-player.service';
import { RevisePLayerResourceType } from '@root-store';
import * as _ from 'lodash';
// tslint:disable-next-line:nx-enforce-module-boundaries
import {
  TqInterface,
  RevisePlayerResourceInterface,
  RevisePlayerSubResourceInterface,
  TqState,
  ResourceState
} from 'apps/main/src/app/core/root-store';
import { take } from 'rxjs/operators';

@Component({
  selector: 'app-revise',
  templateUrl: './revise.component.html',
  styleUrls: ['./revise.component.scss']
})
export class ReviseComponent implements OnInit, OnDestroy {
  subResourceDestroy: Subscription;
  data: any;
  playerData;
  public destroy$: Subject<boolean> = new Subject<boolean>();
  resourceData: RevisePlayerResourceInterface[];
  introData: Observable<Object>;  
  @Output() summary: EventEmitter<any> = new EventEmitter<any>();
  headerData = {
    text: "Habitats and Adaptations",
    type: "back",
    classes: "small",
    link: "/browseBook/main"
  }
  summaryData;
  videoData;
  quizData;
  tqref;
  selectedTq;
  selectedResource: any;
  showSummary = false;
  reviseSummary = false;
  counterTq: any = 0;
  counterResource: any = 0;
  overViewShow: any = false;
  lockedType: any = true;
  resourceList = [];
  noteListShow: any = false;
  public editOpen = false;
  public noteData: any;
  selectedNoteObj: any;
  continueShow: any = 'hide_continue';
  noteLength = 0;
  // load the continue on the basis of the config_continue. By default, it should be hidden
  config_continue = {
    onQuestLoad: 'hide_continue',
    video_load: 'hide_continue',
    quiz_load: 'hide_continue',
    onQuizComplete: 'hide_continue',
    onQuestAttempt: 'show_continue',
    onQuestComplete: 'show_continue',
    summary_load: 'show_continue'
  };
  display_config = this.config_continue;
  // if display_config is present, it will be dynamic

  constructor(
    private playerFacade: PlayerFacade,
    public api: LessonPlayerService,
    private sharedModuleEventHandlerService: SharedModuleEventHandlerService
  ) {}

  ngOnInit(): void {
    this.data = [
      {
        resourceId: 220,
        title:
          'How do we separate solid materials based on the size of the particles?',
        data:
          '[{"type":"text","data":"<p><strong>Solid materials are separated using traditional methods like -</strong></p><ul><li>Hand picking: Separation of large sized impurities by using hands.</li><li>Winnowing: Separation of heavier and lighter components by wind.</li><li>Threshing: Separation of grains from stalks by beating.</li><li>Sieving:<strong> </strong>Separation of<strong> </strong>fine useful particles from big impurities by using sieve.</li></ul>","posX":0,"posY":0,"bg":"","style":{}},{"type":"image","data":"https://white-warrior.s3.ap-south-1.amazonaws.com/tce-app/summary/1588743294439-3847.png","posX":0,"posY":0,"bg":"","style":{}}]'
      },
      {
        resourceId: 220,
        title:
          'How do we separate solid materials based on the size of the particles?',
        data:
          '[{"type":"text","data":"<p><strong>Solid materials are separated using traditional methods like -</strong></p><ul><li>Hand picking: Separation of large sized impurities by using hands.</li><li>Winnowing: Separation of heavier and lighter components by wind.</li><li>Threshing: Separation of grains from stalks by beating.</li><li>Sieving:<strong> </strong>Separation of<strong> </strong>fine useful particles from big impurities by using sieve.</li></ul>","posX":0,"posY":0,"bg":"","style":{}},{"type":"image","data":"https://white-warrior.s3.ap-south-1.amazonaws.com/tce-app/summary/1588743294439-3847.png","posX":0,"posY":0,"bg":"","style":{}},{"type":"text","data":"<p><strong>Solid materials are separated using traditional methods like -</strong></p><ul><li>Hand picking: Separation of large sized impurities by using hands.</li><li>Winnowing: Separation of heavier and lighter components by wind.</li><li>Threshing: Separation of grains from stalks by beating.</li><li>Sieving:<strong> </strong>Separation of<strong> </strong>fine useful particles from big impurities by using sieve.</li></ul>","posX":0,"posY":0,"bg":"","style":{}},{"type":"image","data":"https://white-warrior.s3.ap-south-1.amazonaws.com/tce-app/summary/1588743294439-3847.png","posX":0,"posY":0,"bg":"","style":{}}]'
      },
      {
        resourceId: 221,
        title:
          'How do we separate solid materials based on the volume of the particles?',
        data:
          '[{"type":"text","data":"<p><strong>Solid materials are separated using traditional methods like -</strong></p><ul><li>Hand picking: Separation of large sized impurities by using hands.</li><li>Winnowing: Separation of heavier and lighter components by wind.</li><li>Threshing: Separation of grains from stalks by beating.</li><li>Sieving:<strong> </strong>Separation of<strong> </strong>fine useful particles from big impurities by using sieve.</li></ul>","posX":0,"posY":0,"bg":"","style":{}},{"type":"image","data":"https://white-warrior.s3.ap-south-1.amazonaws.com/tce-app/summary/1588743294439-3847.png","posX":0,"posY":0,"bg":"","style":{}},{"type":"image","data":"https://white-warrior.s3.ap-south-1.amazonaws.com/tce-app/summary/1588743294439-3847.png","posX":0,"posY":0,"bg":"","style":{}}]'
      }
    ];

    // this.playerFacade.getProgressiveDisclosure().subscribe((data: any) => {
    //   this.lockedType = data.status;
    // });
    this.lockedType = true;
    this.playerFacade
      .getTqsData()
      .pipe(take(1))
      .subscribe((tqs: TqInterface[]) => {
        this.playerData = tqs;
        this.initializeTq();
      });
    this.playerFacade.getSubResourceByKeyValue('resourceType', 'stunote').subscribe(
      (notes) => this.noteLength = notes.length
    )
  }

  initializeTq() {
    if (this.lockedType) {
      this.setLockedStatus(this.playerData);
    }
    this.counterTq = 0;
    this.counterResource = -1;
    this.setSelectedTq(0, -1);
    this.updatePlayerData();
  }

  editTrigger(obj) {
    this.editOpen = true;
    this.noteData = obj;
  }

  selectedNote(obj) {
    this.selectedNoteObj = obj;
  }

  saveDataNotes(obj){
    const data = {
      title: this.playerFacade.getcurrentTqSelected().label,
      assets: [{
        type: "text", value: obj.textType[0]
      }]
    };
    const subResource: RevisePlayerSubResourceInterface = {
      tqId: this.selectedNoteObj.tqId,
      resourceType: "stunote",
      subResourceId: this.selectedNoteObj.subResourceId,
      data: data
    }
    this.playerFacade.updateSubResourceState(subResource)
    this.playerFacade.getSubResourceByKeyValue('resourceType','video').pipe(take(1)).subscribe(
      (videoOutput: any) => {
        let videoObj = _.cloneDeep(videoOutput);
        videoObj.forEach((element,index1) => {
          if(element.tqId===this.selectedNoteObj.tqId) {
            element.data.interactiveVideo.assets.interactions.forEach((value,index2)=> {
              if(value.subResourceId===this.selectedNoteObj.subResourceId) {
                videoObj[index1].data.interactiveVideo.assets.interactions[index2].action.params.text=obj.textType[0];
                this.playerFacade.updateSubResourceState(videoObj[index1])
              }
            })
          }
        });
      }
    )
  }

  expandMenu(event) {
    this.tqref = this.playerData[event.index];
    this.playerFacade
      .getResourceList(this.tqref.tqId)
      .pipe(takeUntil(this.destroy$))
      .subscribe((resourceList: RevisePlayerResourceInterface[]) => {
        this.resourceList = resourceList;
      });
  }

  setSelectedTq(tqIndex, resIndex) {
    this.counterTq = tqIndex;
    this.counterResource = resIndex;
    this.tqref = this.playerData[this.counterTq];
    this.selectedTq = { tqId: this.tqref.tqId, $tq: this.tqref, data: [] };
    this.setTqStatus(this.selectedTq.$tq, TqState.UNLOCK);
    this.getTqResources();
  }

  getTqResources() {
    this.playerFacade
      .getResourceList(this.selectedTq.tqId)
      .pipe(take(1))
      .subscribe((resourceList: RevisePlayerResourceInterface[]) => {
        this.selectedTq.data = resourceList;
        // this.resourceList =  this.selectedTq.data;
        // console.log(this.resourceList);
        this.expandMenu({ index: this.counterTq });
        this.setResLockedStatus(this.selectedTq.data);
        this.loadResource();
      });
  }

  setLockedStatus(data) {
    for (let index = 0; index < data.length; index++) {
      let value = data[index];
      if (index > 0 && data[index - 1].tqState <= 1) {
        this.setTqStatus(data[index], TqState.LOCK);
      }
      // else {
      //   this.setTqStatus(data[index], TqState.UNLOCK);
      // }
    }
  }

  setResLockedStatus(data) {
    if (this.lockedType) {
      for (let index = 0; index < data.length; index++) {
        let value = data[index];
        if (index > 0 && data[index - 1].resourceState <= 1) {
          this.setResourcState(
            data[index],
            ResourceState.LOCK,
            'setResLockedStatus'
          );
        }
      }
    }
  }

  setTqStatus(tqData, state) {
    if (tqData.tqState < state || tqData.tqState <= 2) {
      let tq = { ...tqData };
      tq.tqState = state;
      this.playerFacade.setCurrentTqSelected(tq);
      this.playerFacade.updateTqState(tq);
    }
  }

  loadResource() {
    this.counterResource = this.counterResource + 1;
    this.selectedResource = this.selectedTq.data[this.counterResource];
    if (!this.selectedResource) {
      this.loadNextTq();
    } else {
      this.setResourcState(
        this.selectedTq.data[this.counterResource],
        ResourceState.UNLOCK,
        'loadResource'
      );
      this.continueShow = this.config_continue[
        this.selectedResource.resourceType + '_load'
      ];
    }
  }

  loadNextTq() {
    // unsubcribe getloadresourece
    console.log('setTqCompleteState');
    this.setTqCompleteState(this.selectedTq.$tq, this.selectedTq.data);
    if (this.counterTq === this.playerData.length - 1) {
      this.setReviseCompleteState();
      // this.playerFacade.setIntroState('closing_pass');
      this.summary.next();
      // load the intro screen when reaching the end of tq
      // need to get this closure from kailash
      //alert('Load Summary');
    } else {
      this.counterTq = this.counterTq + 1;
      this.setSelectedTq(this.counterTq, -1);
    }
  }

  setTqCompleteState(tq, data) {
    console.log(data);
    if (this.isTqComp(data)) {
      console.log('setTqCompleteState:-', data);
      this.setTqStatus(tq, TqState.SUCCESS_COMPLETED);
    } else {
      this.setTqStatus(tq, TqState.FAILED_COMPLETED);
    }
  }

  isTqComp(data) {
    for (let i = 0; i < data.length; i++) {
      if (data[i].resourceState <= 2) {
        return false;
      }
    }
    return true;
  }

  updatePlayerData() {
    this.playerFacade
      .getTqsData()
      .pipe(takeUntil(this.destroy$))
      .subscribe((tqs: TqInterface[]) => {
        this.playerData = tqs;
      });
  }

  setReviseCompleteState() {
    if (this.isResComp()) {
      this.playerFacade.setLessonState('completed_success');
    } else {
      this.playerFacade.setLessonState('closing_fail');
    }
    this.playerFacade.endLessonTimer();

    let state = 0;
    const timeTaken =
      this.playerFacade.totalTimeTaken().minutes * 60 +
      this.playerFacade.totalTimeTaken().seconds;
      const totalDuration = this.playerFacade.getLessonData().duration * 60;
      if (timeTaken <= totalDuration) {
        state = 1;
      }
      this.playerFacade.setTimerState(state);
  }

  isResComp() {
    for (let i = 0; i < this.playerData.length; i++) {
      if (this.playerData[i].tqState <= 2) {
        return false;
      }
    }
    return true;
  }

  onTqSelect(obj) {
    //this.selectedResource = this.selectedTq.data[this.counterResource];
    this.setSelectedTq(obj.selectedTq, obj.selectedResource - 1);
    this.overViewTrigger();
    // this.selectedTq = this.playerData[this.counterTq];
    //this.loadResource();
  }

  overViewTrigger() {
    this.overViewShow = !this.overViewShow;
  }

  computeContinue(event) {
    this.sharedModuleEventHandlerService.broadcast(
      sharedModuleEventHandlerType.CONTINUE_FLOW,
      event
    );
  }

  videoSkip() {
    if (this.selectedResource.resourceType === RevisePLayerResourceType.VIDEO) {
      this.loadResource();
    }
  }

  questionAttemptUpdate(obj) {
    this.continueShow = this.config_continue['onQuestAttempt'];
  }

  questionLoadUpdate(obj) {
    this.continueShow = this.config_continue['onQuestLoad'];
  }

  questionCompleteUpdate(obj) {
    this.continueShow = this.config_continue['onQuestComplete'];
  }

  quizCompleteUpdate(obj) {
    if (this.selectedResource.resourceType === RevisePLayerResourceType.QUIZ) {
      this.continueShow = this.config_continue['onQuizComplete'];
      this.setResourcState(
        this.selectedTq.data[this.counterResource],
        ResourceState.COMPLETED,
        'quizCompleteUpdate'
      );
      this.setResLockedStatus(this.selectedTq.data);
      this.loadResource();
    }
  }

  onSummaryCompleteUpdate(obj) {
    if (this.selectedResource.resourceType === RevisePLayerResourceType.SUMMARY) {
      this.continueShow = this.config_continue['onSummaryComplete'];
      this.setResourcState(
        this.selectedTq.data[this.counterResource],
        ResourceState.COMPLETED,
        'onSummaryCompleteUpdate'
      );
      this.setResLockedStatus(this.selectedTq.data);
      this.loadResource();
    }
  }

  videoCompletedUpdate() {
    this.setResourcState(
      this.selectedTq.data[this.counterResource],
      ResourceState.COMPLETED,
      'videoCompletedUpdate'
    );
    this.setResLockedStatus(this.selectedTq.data);
    this.loadResource();
  }

  setResourcState(resData, state, callee: string) {
    if (resData.resourceState < state || resData.resourceState <= 2) {
      let res = { ...resData };
      res.resourceState = state;
      this.selectedTq.data[this.counterResource] = res;
      this.playerFacade.updateResourceState(res);
    }
  }

  getJson(path) {
    this.api.getJsonData(path).subscribe(res => res);
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}
