import { Component, OnInit, Input } from "@angular/core";

@Component({
  // tslint:disable-next-line: component-selector
  selector: "app-header",
  templateUrl: "./header.component.html",
  styleUrls: ["./header.component.scss"],
})
export class HeaderComponent implements OnInit {
  public locationHeader: any;
  public hideGraphBar = false;
  @Input() public headerData: any;


  public openOverflow: any = false;
  constructor() { }

  public ngOnInit(): void {
    this.locationHeader = window.location.href;
    if (this.locationHeader.includes("browseBook")) {
      this.hideGraphBar = true;
    }

  }
}


