import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnDestroy
} from '@angular/core';
import {
  SharedModuleEventHandlerService,
  sharedModuleEventHandlerType
} from '@shared-event-handler';
import { Subject } from 'rxjs';
import { PlayerFacade } from '../../lesson-player-facade';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-note-list',
  templateUrl: './note-list.component.html',
  styleUrls: ['./note-list.component.scss']
})
export class NoteListComponent implements OnInit, OnDestroy {
  @Input() data;
  @Output() closeModal = new EventEmitter();
  @Output() editTriggerModal = new EventEmitter();
  @Output() selectedNoteData = new EventEmitter();
  noteList = [];
  destroy$: Subject<boolean> = new Subject<boolean>();
  public noteData: any;
  public editOpen = false;
  constructor(
    private sharedModuleEventHandlerService: SharedModuleEventHandlerService,
    private playerFacade: PlayerFacade
  ) {
  }

  ngOnInit(): void {
    this.playerFacade
      .getSubResourceByKeyValue('resourceType', 'stunote')
      .pipe(takeUntil(this.destroy$))
      .subscribe(notes => {
        this.noteList = notes;
      });
  }

  closeAccordion() {
    this.closeModal.emit()
  }

  editTrigger(obj) {
    this.editTriggerModal.emit(obj)
  }

  selectedNote(obj) {
    this.selectedNoteData.emit(obj)
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}
