import { Component, OnInit, ChangeDetectionStrategy, Input, Output, EventEmitter } from "@angular/core";
import { sharedModuleEventHandlerType, SharedModuleEventHandlerService } from '@shared-event-handler';

@Component({
  selector: "tce-web-guidance-modal",
  templateUrl: "./guidance-modal.component.html",
  styleUrls: ["./guidance-modal.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GuidanceModalComponent implements OnInit {
  @Output() showTrigger = new EventEmitter();
  @Output() hiddenTrigger = new EventEmitter();
 
  constructor(private sharedModuleEventHandlerService: SharedModuleEventHandlerService) { }

  public ngOnInit(): void {
  }

  showAgain() {
    this.showTrigger.emit();
  }

  hiddenAgain() {
    this.hiddenTrigger.emit();
  }

}


